import React, {useState, useContext} from 'react'
import { UserContext } from "../context/UserProvider"



function AllIssues(props) {
    
    const { 
        title, 
        description,
        userString 
    } = props
    
    const {changeUpVotes, changeDownVotes, voteCount, disable, disableDownVote, addend, upVote, downVote} = useContext(UserContext)
    
    return (
        <div style={{border: "1px solid black", padding: "5px"}}>
            <h2>@{userString}'s Post:</h2>
            <h3>Title: {title}</h3>
            <p>Description: {description}</p>
            <button onClick={() => upVote()} disabled={disable}>Upvote</button>
            <button onClick={() => downVote()} disabled={disableDownVote}>Downvote</button>
            <p>Total Votes: {voteCount}</p>
        </div>
    )
}

export default AllIssues